@import url(//db.onlinewebfonts.com/c/89248b0583721153668df86453b579de?family=Halyard+Text);
body{margin:48px;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Halyard Text"}body{background-color:#000;margin:inherit}.black-background{background-color:black}.white-background{background-color:white}.full-width{width:100%}.no-margin{margin:0}.center-text{text-align:center}.flex{display:flex}.relative{position:relative}#page-header{display:flex;background-color:#007bff}#page-header img{width:15%;min-width:150px;padding:20px}#loading-screen-container{width:40%;margin-top:100px;position:absolute;left:30%}#loading-screen-container img{margin-bottom:20px}#chronotope-map-section{width:83%;margin-left:auto;margin-right:auto;padding-top:100px;padding-bottom:1px}#chronotope-map-title{color:white;background-color:black;border:2.5px solid #007bff;padding-bottom:5px;padding-left:5px}#chronotope-map-container{max-height:500px;overflow:auto;border-bottom:1.7px solid #007bff}#chronotope-map-container::-webkit-scrollbar{color:#007bff}#chronotope-map-container::-webkit-scrollbar-track{background:#f2f2f2;border-radius:10px;opacity:0.1;width:1px}#chronotope-map-container::-webkit-scrollbar-thumb{background:#007bff;border-radius:10px;opacity:0.1;width:1px}#chronotope-map-container::-webkit-scrollbar-thumb:hover{background:#1486ff}.segment{width:100%;display:flex;background-color:#fbfbfb}.segment p{display:inline-block;margin-top:0px;margin-bottom:0px}.segment .segment-group-column{width:1%}.segment .segment-name{width:15%;font-size:12px}.chronotope-data-container{width:82%;display:inline-block;position:relative;margin-right:1%;display:flex;align-items:center}.chronotope-data{position:absolute}.chronotope-data:hover{z-index:1;cursor:pointer}.chronotope-data>span:hover{border-radius:50%;border:2px solid #009444}.highlight-node{border-radius:50%;border:2px solid #009444;z-index:1}.chronotope-node{height:0.5em;width:0.5em;border-radius:50%;display:inline-block}#toolTip-container{margin-top:10px;background-color:white;width:30%;margin-left:auto;padding:10px;border-radius:17px;border:5px solid #007bff;margin-bottom:10px;height:100px;display:flex;align-items:center}.fade-out-opacity{opacity:0;transition:opacity 1.5s}.fade-in-opacity{opacity:1;transition:opacity 1.5s}.white-background-fadeIn{background-color:white;transition:background-color 1.5s linear}.show-visible-fade{visibility:visible;opacity:1;transition:visibility 2.5s, opacity 2.5s}.hide-visible{visibility:hidden;opacity:0}@media (max-width: 1080px){#toolTip-container{width:50%;height:inherit}}@media (max-width: 670px){#toolTip-container{width:100%;justify-content:center}.segment{min-width:500px}.segment-name{margin-right:4%}}@media (max-width: 568px){#chronotope-map-title{text-align:center}}

